import React from "react";
import PropTypes from "prop-types";

// Define pattern variants
const PATTERN_VARIANTS = {
  DOTS: "dots",
  WAVES: "waves",
  CHECKERBOARD: "checkerboard",
  STRIPES: "stripes",
  SUNBURST: "sunburst",
  GRID: "grid",
  STARS: "stars",
};

// Define color themes
const COLOR_THEMES = {
  LIGHT: "light",
  DARK: "dark",
  CUSTOM: "custom",
};

// Define pattern sizes
const PATTERN_SIZES = {
  SMALL: "small",
  MEDIUM: "medium",
  LARGE: "large",
};

// Add new constants
const PATTERN_BORDER_RADIUS = {
  NONE: "none",
  SMALL: "small",
  MEDIUM: "medium",
  LARGE: "large",
  FULL: "full",
};

const PATTERN_ANIMATIONS = {
  NONE: "none",
  PULSE: "pulse",
  SPIN: "spin",
  BOUNCE: "bounce",
  FADE: "fade",
};

// Helper to generate styles based on props
const getPatternStyle = ({ variant, color, size }) => {
  switch (variant) {
    case PATTERN_VARIANTS.DOTS:
      return {
        backgroundColor: color.background,
        '--foreground-color': color.foreground,
        opacity: 1,
        backgroundSize: size === PATTERN_SIZES.SMALL ? "25px 25px" : size === PATTERN_SIZES.MEDIUM ? "50px 50px" : "100px 100px",
      };
    case PATTERN_VARIANTS.WAVES:
      return {
        backgroundColor: color.background,
        '--foreground-color': color.foreground,
        opacity: 1,
        backgroundSize: size === PATTERN_SIZES.SMALL ? "10px 40px" : size === PATTERN_SIZES.MEDIUM ? "10px 60px" : "10px 80px",
      };
    case PATTERN_VARIANTS.STRIPES:
      return {
        backgroundColor: color.background,
        '--foreground-color': color.foreground,
        opacity: 1,
        backgroundSize: size === PATTERN_SIZES.SMALL ? "10px 10px" : size === PATTERN_SIZES.MEDIUM ? "20px 20px" : "40px 40px",
      };
    case PATTERN_VARIANTS.STARS:
      return {
        backgroundColor: color.background,
        '--foreground-color': color.foreground,
        opacity: 1,
        backgroundSize: size === PATTERN_SIZES.SMALL ? "30px 30px" : size === PATTERN_SIZES.MEDIUM ? "60px 60px" : "90px 90px",
      };
    case PATTERN_VARIANTS.GRID:
      return {
        backgroundColor: color.background,
        '--foreground-color': color.foreground,
        opacity: 1,
        backgroundSize: size === PATTERN_SIZES.SMALL ? "20px 20px" : size === PATTERN_SIZES.MEDIUM ? "40px 40px" : "60px 60px",
      };
    case PATTERN_VARIANTS.SUNBURST:
      const s = size === PATTERN_SIZES.SMALL ? "100px" :
        size === PATTERN_SIZES.MEDIUM ? "150px" : "200px";

      const gradient = `${color.foreground} 0% 5%, ${color.background} 6% 15%, 
        ${color.foreground} 16% 25%, ${color.background} 26% 35%,
        ${color.foreground} 36% 45%, ${color.background} 46% 55%, 
        ${color.foreground} 56% 65%, ${color.background} 66% 75%,
        ${color.foreground} 76% 85%, ${color.background} 86% 95%, 
        transparent 96%`;

      return {
        backgroundColor: color.foreground,
        backgroundImage: `
          radial-gradient(50% 50% at 100% 0, ${gradient}),
          radial-gradient(50% 50% at 0 100%, ${gradient}),
          radial-gradient(50% 50%, ${gradient}),
          radial-gradient(50% 50%, ${gradient})
        `,
        backgroundSize: `${s} ${s}`,
        opacity: 1,
        backgroundPosition: `0 0, 0 0, 0 0, calc(${s} / 2) calc(${s} / 2)`,
      };

    default:
      return {};
  }
};

// Main Pattern component
const Pattern = ({
  variant = PATTERN_VARIANTS.DOTS,
  color = { background: "#e5e5f7", foreground: "#444cf7" },
  size = PATTERN_SIZES.MEDIUM,
  borderRadius = PATTERN_BORDER_RADIUS.NONE,
  opacity = 1,
  animation = PATTERN_ANIMATIONS.NONE,
  animationDuration = 2,
  blur = 0,
  rotate = 0,
  className = "",
  style = {},
  ...props
}) => {
  const patternStyle = getPatternStyle({ variant, color, size });

  const combinedStyle = {
    ...patternStyle,
    ...style,
    borderRadius: getBorderRadiusValue(borderRadius),
    opacity,
    filter: blur ? `blur(${blur}px)` : undefined,
    animation: animation !== PATTERN_ANIMATIONS.NONE ?
      `${animation} ${animationDuration}s infinite` : undefined,
  };

  return (
    <div
      className={`lebify-pattern lebify-pattern-${variant} ${animation !== PATTERN_ANIMATIONS.NONE ? `lebify-pattern-${animation}` : ''} ${className}`}
      style={combinedStyle}
      {...props}
    ></div>
  );
};

// Helper function to get border radius value
const getBorderRadiusValue = (borderRadius) => {
  switch (borderRadius) {
    case PATTERN_BORDER_RADIUS.SMALL: return '5px';
    case PATTERN_BORDER_RADIUS.MEDIUM: return '25px';
    case PATTERN_BORDER_RADIUS.LARGE: return '100px';
    case PATTERN_BORDER_RADIUS.FULL: return '9999px';
    default: return '0';
  }
};

Pattern.propTypes = {
  variant: PropTypes.oneOf(Object.values(PATTERN_VARIANTS)),
  color: PropTypes.shape({
    background: PropTypes.string,
    foreground: PropTypes.string,
  }),
  size: PropTypes.oneOf(Object.values(PATTERN_SIZES)),
  className: PropTypes.string,
  style: PropTypes.object,
  borderRadius: PropTypes.oneOf(Object.values(PATTERN_BORDER_RADIUS)),
  opacity: PropTypes.number,
  animation: PropTypes.oneOf(Object.values(PATTERN_ANIMATIONS)),
  animationDuration: PropTypes.number,
  blur: PropTypes.number,
  rotate: PropTypes.number,
};

// Export constants for external use
Pattern.VARIANTS = PATTERN_VARIANTS;
Pattern.SIZES = PATTERN_SIZES;
Pattern.COLOR_THEMES = COLOR_THEMES;
Pattern.BORDER_RADIUS = PATTERN_BORDER_RADIUS;
Pattern.ANIMATIONS = PATTERN_ANIMATIONS;

export default Pattern;