import React, { useState, useRef, useEffect } from "react";
import PropTypes from "prop-types";
import "./Input.css";

// Group input variants by collections for better organization
const INPUT_VARIANTS = {
  // Basic variants
  BASIC: ["primary", "secondary", "tertiary", "basic"],
  // Lebanese-inspired collections
  CEDAR: ["cedar", "cedar-light", "cedar-dark"],
  MEDITERRANEAN: ["sea", "coast", "azure"],
  MOUNTAIN: ["stone", "earth", "terracotta"],
  BEIRUT: ["urban", "concrete", "steel"],
  CULINARY: ["olive", "saffron", "grape"],
  FESTIVAL: ["festive", "celebration", "heritage"],
  CRAFT: ["copper", "silver", "ceramic"],
  MODERN: ["tech", "digital", "startup"],
  // Modern variants
  CONTEMPORARY: [
    "glass",
    "neomorphic",
    "gradient",
    "minimal",
    "dark",
    "pill",
    "flat",
  ],
};

// Define input types
const INPUT_TYPES = {
  TEXT: "text",
  PASSWORD: "password",
  EMAIL: "email",
  NUMBER: "number",
  TEL: "tel",
  URL: "url",
  SEARCH: "search",
  DATE: "date",
  TIME: "time",
  DATETIME: "datetime-local",
  MONTH: "month",
  WEEK: "week",
  COLOR: "color",
  FILE: "file",
  TEXTAREA: "textarea",
  SELECT: "select",
};

// Define border radius options
const BORDER_RADIUS = {
  NONE: "none", // No border radius (square)
  SMALL: "small", // Slight rounding (4px)
  MEDIUM: "medium", // Default rounding (6px)
  LARGE: "large", // More pronounced rounding (8px)
  PILL: "pill", // Pill shape (9999px)
};

// Define input styles
const INPUT_STYLES = {
  DEFAULT: "default",
  FLOATING: "floating",
  ANIMATED_BORDER: "animated-border",
  UNDERLINED: "underlined",
  SEARCH: "search",
};

// Define input sizes - adding compact size between small and medium
const INPUT_SIZES = {
  SMALL: "small",
  COMPACT: "compact", // New size between small and medium
  MEDIUM: "medium",
  LARGE: "large",
};

// Flatten variants for PropTypes validation
const ALL_VARIANTS = Object.values(INPUT_VARIANTS).flat();
const ALL_INPUT_TYPES = Object.values(INPUT_TYPES);
const ALL_BORDER_RADIUS = Object.values(BORDER_RADIUS);
const ALL_INPUT_STYLES = Object.values(INPUT_STYLES);

// Main Input component
const Input = ({
  id,
  name,
  type = INPUT_TYPES.TEXT,
  value,
  defaultValue,
  placeholder = "",
  label,
  helperText,
  error = false,
  success = false,
  disabled = false,
  required = false,
  readOnly = false,
  autoFocus = false,
  autoComplete = "off",
  variant = "basic",
  size = INPUT_SIZES.MEDIUM,
  borderRadius = BORDER_RADIUS.MEDIUM,
  inputStyle = INPUT_STYLES.DEFAULT,
  className = "",
  style = {},
  onChange,
  onFocus,
  onBlur,
  onKeyDown,
  onKeyUp,
  onKeyPress,
  leftIcon = null,
  rightIcon = null,
  maxLength,
  minLength,
  pattern,
  min,
  max,
  step,
  multiple = false,
  accept,
  rows = 4,
  cols,
  options = [],
  showCharacterCount = false,
  fullWidth = false,
  characterCountPosition = "right",
  ...props
}) => {
  // State for password visibility toggle
  const [showPassword, setShowPassword] = useState(false);
  // State for character count
  const [charCount, setCharCount] = useState(
    value?.length || defaultValue?.length || 0
  );
  // State for file name display
  const [fileName, setFileName] = useState("");
  // Ref for the input element
  const inputRef = useRef(null);

  // Handle character count update
  useEffect(() => {
    if (value !== undefined) {
      setCharCount(value.length);
    }
  }, [value]);

  // Handle input change
  const handleChange = (e) => {
    if (onChange) {
      onChange(e);
    }

    // Update character count for uncontrolled inputs
    if (value === undefined) {
      setCharCount(e.target.value.length);
    }

    // Handle file input
    if (type === INPUT_TYPES.FILE && e.target.files.length > 0) {
      const files = Array.from(e.target.files);
      setFileName(files.map((file) => file.name).join(", "));
    }
  };

  // Toggle password visibility
  const togglePasswordVisibility = () => {
    setShowPassword(!showPassword);
  };

  // Determine the actual input type (for password toggle)
  const getInputType = () => {
    if (type === INPUT_TYPES.PASSWORD) {
      return showPassword ? INPUT_TYPES.TEXT : INPUT_TYPES.PASSWORD;
    }
    return type;
  };

  // Build class names
  const containerClass = `
    lebify-input-container
    ${fullWidth ? "lebify-input-full-width" : ""}
    ${className}
  `.trim();

  const inputClass = `
    lebify-input
    lebify-input-${size}
    lebify-input-${variant}
    lebify-input-border-${borderRadius}
    ${error ? "lebify-input-error" : ""}
    ${success ? "lebify-input-success" : ""}
    ${disabled ? "lebify-input-disabled" : ""}
    ${inputStyle !== INPUT_STYLES.DEFAULT ? `lebify-input-${inputStyle}` : ""}
    ${leftIcon ? "lebify-input-with-icon-left" : ""}
    ${
      rightIcon || type === INPUT_TYPES.PASSWORD
        ? "lebify-input-with-icon-right"
        : ""
    }
  `.trim();

  const helperTextClass = `
    lebify-input-helper-text
    ${error ? "lebify-input-helper-text-error" : ""}
    ${success ? "lebify-input-helper-text-success" : ""}
  `.trim();

  // Render the appropriate input based on type
  const renderInput = () => {
    // Common props for all input types
    const commonProps = {
      id,
      name,
      disabled,
      required,
      readOnly,
      autoFocus,
      autoComplete,
      className: inputClass,
      style,
      onChange: handleChange,
      onFocus,
      onBlur,
      onKeyDown,
      onKeyUp,
      onKeyPress,
      ref: inputRef,
      "aria-invalid": error,
      "aria-describedby": helperText ? `${id}-helper-text` : undefined,
      ...props,
    };

    // For controlled components
    if (value !== undefined) {
      commonProps.value = value;
    } else if (defaultValue !== undefined) {
      commonProps.defaultValue = defaultValue;
    }

    // Render textarea
    if (type === INPUT_TYPES.TEXTAREA) {
      return (
        <textarea
          {...commonProps}
          placeholder={placeholder}
          rows={rows}
          cols={cols}
          maxLength={maxLength}
          minLength={minLength}
        />
      );
    }

    // Render select
    if (type === INPUT_TYPES.SELECT) {
      return (
        <select {...commonProps}>
          {placeholder && (
            <option value="" disabled>
              {placeholder}
            </option>
          )}
          {options.map((option, index) => (
            <option key={index} value={option.value}>
              {option.label}
            </option>
          ))}
        </select>
      );
    }

    // Render file input
    if (type === INPUT_TYPES.FILE) {
      return (
        <div className="lebify-file-input-wrapper">
          <input
            {...commonProps}
            type="file"
            className="lebify-file-input"
            accept={accept}
            multiple={multiple}
          />
          <div className="lebify-file-input-button">
            {leftIcon && (
              <span className="lebify-input-icon-left">{leftIcon}</span>
            )}
            <span className="lebify-file-input-text">
              {fileName || placeholder || "Choose file"}
            </span>
          </div>
        </div>
      );
    }

    // Render regular input
    return (
      <input
        {...commonProps}
        type={getInputType()}
        placeholder={placeholder}
        maxLength={maxLength}
        minLength={minLength}
        pattern={pattern}
        min={min}
        max={max}
        step={step}
        accept={accept}
        multiple={multiple}
      />
    );
  };

  // Render the password toggle button
  const renderPasswordToggle = () => {
    if (type !== INPUT_TYPES.PASSWORD) return null;

    return (
      <button
        type="button"
        className="lebify-input-password-toggle"
        onClick={togglePasswordVisibility}
        tabIndex="-1"
        aria-label={showPassword ? "Hide password" : "Show password"}
      >
        {showPassword ? "Hide" : "Show"}
      </button>
    );
  };

  // Render character counter
  const renderCharacterCounter = () => {
    if (!showCharacterCount) return null;

    const countClass = `lebify-input-character-count lebify-input-character-count-${characterCountPosition}`;

    return (
      <div className={countClass}>
        {charCount}
        {maxLength && ` / ${maxLength}`}
      </div>
    );
  };

  // Render the complete input component
  return (
    <div className={containerClass}>
      {/* Label */}
      {label && inputStyle !== INPUT_STYLES.FLOATING && (
        <label htmlFor={id} className="lebify-input-label">
          {label}
          {required && <span className="lebify-input-required">*</span>}
        </label>
      )}

      {/* Input wrapper */}
      <div className="lebify-input-wrapper">
        {/* Left icon */}
        {leftIcon && <span className="lebify-input-icon-left">{leftIcon}</span>}

        {/* Input element */}
        {inputStyle === INPUT_STYLES.FLOATING ? (
          <div className="lebify-input-floating-container">
            {renderInput()}
            <label htmlFor={id} className="lebify-input-floating-label">
              {label}
              {required && <span className="lebify-input-required">*</span>}
            </label>
          </div>
        ) : (
          renderInput()
        )}

        {/* Right icon or password toggle */}
        {type === INPUT_TYPES.PASSWORD ? (
          <span className="lebify-input-icon-right">
            {renderPasswordToggle()}
          </span>
        ) : (
          rightIcon && (
            <span className="lebify-input-icon-right">{rightIcon}</span>
          )
        )}
      </div>

      {/* Helper text and character counter */}
      <div className="lebify-input-footer">
        {helperText && (
          <div
            className={`lebify-input-helper-text ${
              error ? "lebify-input-helper-text-error" : ""
            } ${success ? "lebify-input-helper-text-success" : ""}`}
          >
            {helperText}
          </div>
        )}
        {renderCharacterCounter()}
      </div>
    </div>
  );
};

Input.propTypes = {
  id: PropTypes.string,
  name: PropTypes.string,
  type: PropTypes.oneOf(ALL_INPUT_TYPES),
  value: PropTypes.any,
  defaultValue: PropTypes.any,
  placeholder: PropTypes.string,
  label: PropTypes.node,
  helperText: PropTypes.node,
  error: PropTypes.bool,
  success: PropTypes.bool,
  disabled: PropTypes.bool,
  required: PropTypes.bool,
  readOnly: PropTypes.bool,
  autoFocus: PropTypes.bool,
  autoComplete: PropTypes.string,
  variant: PropTypes.oneOf(ALL_VARIANTS),
  size: PropTypes.oneOf(Object.values(INPUT_SIZES)),
  borderRadius: PropTypes.oneOf(ALL_BORDER_RADIUS),
  inputStyle: PropTypes.oneOf(ALL_INPUT_STYLES),
  className: PropTypes.string,
  style: PropTypes.object,
  onChange: PropTypes.func,
  onFocus: PropTypes.func,
  onBlur: PropTypes.func,
  onKeyDown: PropTypes.func,
  onKeyUp: PropTypes.func,
  onKeyPress: PropTypes.func,
  leftIcon: PropTypes.node,
  rightIcon: PropTypes.node,
  maxLength: PropTypes.number,
  minLength: PropTypes.number,
  pattern: PropTypes.string,
  min: PropTypes.oneOfType([PropTypes.number, PropTypes.string]),
  max: PropTypes.oneOfType([PropTypes.number, PropTypes.string]),
  step: PropTypes.oneOfType([PropTypes.number, PropTypes.string]),
  multiple: PropTypes.bool,
  accept: PropTypes.string,
  rows: PropTypes.number,
  cols: PropTypes.number,
  options: PropTypes.arrayOf(
    PropTypes.shape({
      value: PropTypes.string.isRequired,
      label: PropTypes.string.isRequired,
    })
  ),
  showCharacterCount: PropTypes.bool,
  fullWidth: PropTypes.bool,
  characterCountPosition: PropTypes.oneOf(["left", "right"]),
};

// Export the variants and types for external use
Input.VARIANTS = INPUT_VARIANTS;
Input.TYPES = INPUT_TYPES;
Input.BORDER_RADIUS = BORDER_RADIUS;
Input.STYLES = INPUT_STYLES;

// Export sizes
Input.SIZES = INPUT_SIZES;

export default Input;
