import React, { useState, useRef } from "react";
import PropTypes from "prop-types";

// Group button variants by collections for better organization
const BUTTON_VARIANTS = {
  // Basic variants
  BASIC: ["primary", "secondary", "tertiary", "basic"],
  // Lebanese-inspired collections
  CEDAR: ["cedar", "cedar-light", "cedar-dark"],
  MEDITERRANEAN: ["sea", "coast", "azure"],
  MOUNTAIN: ["stone", "earth", "terracotta"],
  BEIRUT: ["urban", "concrete", "steel"],
  CULINARY: ["olive", "saffron", "grape"],
  FESTIVAL: ["festive", "celebration", "heritage"],
  CRAFT: ["copper", "silver", "ceramic"],
  MODERN: ["tech", "digital", "startup"],
};

// Define button types
const BUTTON_TYPES = {
  FILLED: "filled", // Default solid background
  OUTLINED: "outlined", // Border only, transparent background
  GHOST: "ghost", // No background, no border, just text color
};

// Define hover effects
const HOVER_EFFECTS = {
  // Basic effects
  DEFAULT: "default",
  NONE: "none",
  // Advanced effects
  THREE_D_ROTATE: "3d-rotate",
  MAGNETIC: "magnetic",
  SHEEN: "sheen",
  NEON: "neon",
  REVEAL: "reveal",
  EXPAND: "expand",
  LIQUID: "liquid",
  SLICE: "slice",
  BOUNCE: "bounce",
  GLITCH: "glitch",
  BORDER_SPIN: "border-spin",
};

// Define icon positions
const ICON_POSITIONS = {
  LEFT: "left",
  RIGHT: "right",
};

// Define border radius options
const BORDER_RADIUS = {
  NONE: "none", // No border radius (square)
  SMALL: "small", // Slight rounding (4px)
  MEDIUM: "medium", // Default rounding (6px)
  LARGE: "large", // More pronounced rounding (8px)
  PILL: "pill", // Pill shape (9999px)
  ROUNDED: "rounded", // Fully rounded (50%)
};

// Define loading spinner types
const LOADING_SPINNER = {
  DOTS: "dots", // Three dots bouncing
  CIRCLE: "circle", // Circular spinner
  PULSE: "pulse", // Pulsing circle
  BARS: "bars", // Loading bars
  ELLIPSIS: "ellipsis", // Text ellipsis (...)
};

// Flatten variants for PropTypes validation
const ALL_VARIANTS = Object.values(BUTTON_VARIANTS).flat();
const ALL_BUTTON_TYPES = Object.values(BUTTON_TYPES);
const ALL_HOVER_EFFECTS = Object.values(HOVER_EFFECTS);
const ALL_ICON_POSITIONS = Object.values(ICON_POSITIONS);
const ALL_BORDER_RADIUS = Object.values(BORDER_RADIUS);
const ALL_LOADING_SPINNER = Object.values(LOADING_SPINNER);

// Helper to get collection name from variant
const getCollectionFromVariant = (variant) => {
  return (
    Object.entries(BUTTON_VARIANTS)
      .find(([_, variants]) => variants.includes(variant))?.[0]
      ?.toLowerCase() || "basic"
  );
};

// Main Button component that supports all variants
const Button = ({
  children,
  variant = "primary",
  buttonType = BUTTON_TYPES.FILLED,
  size = "medium",
  disabled = false,
  onClick,
  className = "",
  style = {},
  hoverEffect = HOVER_EFFECTS.DEFAULT,
  icon = null,
  iconPosition = ICON_POSITIONS.LEFT,
  iconOnly = false,
  iconSpacing = "0.5rem",
  iconClassName = "",
  iconStyle = {},
  borderRadius = BORDER_RADIUS.MEDIUM,
  loading = false,
  loadingText = null,
  loadingSpinner = LOADING_SPINNER.CIRCLE,
  loadingPosition = ICON_POSITIONS.LEFT,
  hideTextWhenLoading = false,
  ...props
}) => {
  // Determine if we have content besides the icon
  const hasContent = React.Children.count(children) > 0;

  // Determine if we should show text during loading
  const showTextWhenLoading =
    !hideTextWhenLoading && (loadingText !== null || hasContent);

  // Build class names
  const buttonClass = `
    lebify-button 
    lebify-button-${variant} 
    lebify-button-${size}
    lebify-button-${buttonType}
    lebify-button-radius-${borderRadius}
    ${disabled ? "lebify-button-disabled" : ""}
    ${loading ? "lebify-button-loading" : ""}
    ${loading && !showTextWhenLoading ? "lebify-button-loading-no-text" : ""}
    ${
      hoverEffect !== HOVER_EFFECTS.DEFAULT ? `lebify-hover-${hoverEffect}` : ""
    }
    ${icon ? "lebify-button-with-icon" : ""}
    ${
      icon && iconPosition === ICON_POSITIONS.LEFT
        ? "lebify-button-icon-left"
        : ""
    }
    ${
      icon && iconPosition === ICON_POSITIONS.RIGHT
        ? "lebify-button-icon-right"
        : ""
    }
    ${iconOnly ? "lebify-button-icon-only" : ""}
    ${className}
  `
    .trim()
    .replace(/\s+/g, " ");

  // Icon class
  const iconClass = `
    lebify-button-icon
    lebify-button-icon-${size}
    ${iconClassName}
  `
    .trim()
    .replace(/\s+/g, " ");

  // Icon spacing style
  const iconSpacingStyle = {
    ...iconStyle,
    ...(iconPosition === ICON_POSITIONS.LEFT && !iconOnly
      ? { marginRight: iconSpacing }
      : {}),
    ...(iconPosition === ICON_POSITIONS.RIGHT && !iconOnly
      ? { marginLeft: iconSpacing }
      : {}),
  };

  // Loading spacing style
  const loadingSpacingStyle = {
    ...(loadingPosition === ICON_POSITIONS.LEFT && showTextWhenLoading
      ? { marginRight: iconSpacing }
      : {}),
    ...(loadingPosition === ICON_POSITIONS.RIGHT && showTextWhenLoading
      ? { marginLeft: iconSpacing }
      : {}),
  };

  // Render loading spinner based on type
  const renderLoadingSpinner = () => {
    switch (loadingSpinner) {
      case LOADING_SPINNER.DOTS:
        return (
          <div className={`lebify-spinner lebify-spinner-dots lebify-spinner-${size}`}>
            <div className="dot"></div>
            <div className="dot"></div>
            <div className="dot"></div>
          </div>
        );
      case LOADING_SPINNER.PULSE:
        return (
          <div className={`lebify-spinner lebify-spinner-pulse lebify-spinner-${size}`}>
            <div className="pulse"></div>
          </div>
        );
      case LOADING_SPINNER.BARS:
        return (
          <div className={`lebify-spinner lebify-spinner-bars lebify-spinner-${size}`}>
            <div className="bar"></div>
            <div className="bar"></div>
            <div className="bar"></div>
            <div className="bar"></div>
          </div>
        );
      case LOADING_SPINNER.ELLIPSIS:
        return (
          <div className={`lebify-spinner lebify-spinner-ellipsis lebify-spinner-${size}`}>
            <div className="dot"></div>
            <div className="dot"></div>
            <div className="dot"></div>
          </div>
        );
      default: // LOADING_SPINNER.CIRCLE
        return (
          <div className={`lebify-spinner lebify-spinner-circle lebify-spinner-${size}`}>
            <svg viewBox="0 0 50 50">
              <circle
                cx="25"
                cy="25"
                r="20"
                fill="none"
                strokeWidth="5"
              ></circle>
            </svg>
          </div>
        );
    }
  };

  // Render the button with icon
  return (
    <button
      className={buttonClass}
      onClick={disabled || loading ? undefined : onClick}
      disabled={disabled}
      style={style}
      {...(iconOnly &&
        !loading && {
          "aria-label":
            props["aria-label"] ||
            (typeof children === "string" ? children : undefined),
        })}
      {...props}
    >
      <div className="lebify-button-content">
        {/* Left-positioned loading spinner */}
        {loading && loadingPosition === ICON_POSITIONS.LEFT && (
          <span
            className="lebify-button-loading-spinner"
            style={loadingSpacingStyle}
          >
            {renderLoadingSpinner()}
          </span>
        )}

        {/* Left-positioned icon (when not loading) */}
        {!loading &&
          icon &&
          (iconPosition === ICON_POSITIONS.LEFT || iconOnly) && (
            <span className={iconClass} style={iconSpacingStyle}>
              {icon}
            </span>
          )}

        {/* Button text content */}
        {(!loading || showTextWhenLoading) && (
          <span className="lebify-button-text">
            {loading && loadingText
              ? loadingText
              : !iconOnly && hasContent && children}
          </span>
        )}

        {/* Right-positioned icon (when not loading) */}
        {!loading &&
          icon &&
          iconPosition === ICON_POSITIONS.RIGHT &&
          !iconOnly && (
            <span className={iconClass} style={iconSpacingStyle}>
              {icon}
            </span>
          )}

        {/* Right-positioned loading spinner */}
        {loading && loadingPosition === ICON_POSITIONS.RIGHT && (
          <span
            className="lebify-button-loading-spinner"
            style={loadingSpacingStyle}
          >
            {renderLoadingSpinner()}
          </span>
        )}
      </div>
    </button>
  );
};

Button.propTypes = {
  children: PropTypes.node,
  variant: PropTypes.oneOf(ALL_VARIANTS),
  buttonType: PropTypes.oneOf(ALL_BUTTON_TYPES),
  size: PropTypes.oneOf(["small", "medium", "large"]),
  disabled: PropTypes.bool,
  onClick: PropTypes.func,
  className: PropTypes.string,
  style: PropTypes.object,
  hoverEffect: PropTypes.oneOf(ALL_HOVER_EFFECTS),
  icon: PropTypes.node,
  iconPosition: PropTypes.oneOf(ALL_ICON_POSITIONS),
  iconOnly: PropTypes.bool,
  iconSpacing: PropTypes.string,
  iconClassName: PropTypes.string,
  iconStyle: PropTypes.object,
  borderRadius: PropTypes.oneOf(ALL_BORDER_RADIUS),
  loading: PropTypes.bool,
  loadingText: PropTypes.string,
  loadingSpinner: PropTypes.oneOf(ALL_LOADING_SPINNER),
  loadingPosition: PropTypes.oneOf(ALL_ICON_POSITIONS),
  hideTextWhenLoading: PropTypes.bool,
};

// Export the variants and hover effects for external use
Button.VARIANTS = BUTTON_VARIANTS;
Button.TYPES = BUTTON_TYPES;
Button.HOVER_EFFECTS = HOVER_EFFECTS;
Button.ICON_POSITIONS = ICON_POSITIONS;
Button.BORDER_RADIUS = BORDER_RADIUS;
Button.LOADING_SPINNER = LOADING_SPINNER;

export default Button;
